<template>
  <div class="top_Bar">
  	  <div class="estab_top">
        <a href="javascript:void(0)">
          <Icon type="arrow-left-a" color="black" size="22" @click.native="pre" v-if="show"></Icon>
        </a>
        <Tooltip placement="bottom">
            <Icon type="android-person" color="black" size="25"></Icon>
          <div slot="content" class="person_content">
            <p>
              <a href="javascript:void(0)" class="pay">
                <div>
                  <Icon type="pricetag"></Icon>
                  <span class="person_content_font">余额</span>
                  <a href="javascript:void(0)" class="charge" @click="charge">充值</a>
                </div>
                <span>¥ {{info.wallet}}</span>
              </a>
            </p>
            <p><a href="javascript:void(0)"><Icon type="android-person"></Icon><span class="person_content_font">个人中心</span></a></p>
            <p v-if="info.is_agency_user === 0" @click="applyAgent"><a href="javascript:void(0)"><Icon type="android-person-add"></Icon><span class="person_content_font">申请代理商</span></a></p>
            <p><a href="javascript:void(0)" @click="quit"><Icon type="power"></Icon><span class="person_content_font">退出账号</span></a></p>
          </div>
        </Tooltip>
        <Modal
          v-model="agentModal"
          title="申请成为代理商"
        >
          <div style="text-align: center" >
            <p style="margin-top: 0.2rem;position: relative">
              <i class="iconfont icon-yonghuming ic"></i>
              <input  placeholder="姓名" v-model.trim="agentName"  style="width: 3rem;" class="input inpu" />
            </p>
            <p style="margin-top: 0.2rem;position: relative">
              <i class="iconfont icon-renzheng ic"></i>
              <input  placeholder="身份证号" v-model.trim="agentCard"  style="width: 3rem;" class="input inpu" />
            </p>
          </div>
          <div slot="footer" style="text-align: center;padding: 10px 0;">
            <a href="javascript:void(0)" class="submit" @click="submit" v-if="loading">提交</a>
            <a href="javascript:void(0)" class="submit" v-if="!loading">
              提交
              <img src="../../assets/bai.gif" style="width: 20px;max-height: 100%;margin-left: 10px;"/>
            </a>
          </div>
        </Modal>
  	</div>
  </div>
</template>

<script>
  export default {
    data(){
      return{
        info:{},
        agentModal:false,
        agentName:'',
        agentCard:"",
        loading: true,
        id:"",
      }
    },
     computed:{
        show(){
          return this.$store.getters.getPre;
        },
     },
     methods:{
        quit(){
          this.$router.push('/');
       },
       charge(){
         this.$router.push('/charge');
       },
       pre(){
           this.$router.go(-1);
       },
//      申请成为代理商
       applyAgent () {
         this.agentModal = true ;
       },
       submit(){
           this.load = false;
          this.post('system/Agency/normal_agent',{
            id: this.id,
            id_cart: this.agentCard,
            agency_realname: this.agentName
          }).then(res => {
              this.loading = true;
              if (res.data.code === 10000) {
                  this.agentModal = false;
                this.$Modal.success({
                  title: "申请成功",
                  content: '请联系管理员审核'
                });
              } else {
                this.$Modal.success({
                  title: "申请失败",
                  content: res.data.msg
                });
              }
          })
       },
    },
    created(){
      this.post('custom/Own/getCustomInfo', {}).then( res => {
        if ( res.data.code === 10000){
          this.info = res.data.data;
          this.$emit('info',this.info);
          this.id = res.data.data.id;
        }
      })
    },
    mounted(){
          if (this.$route.name==="establish"){
             return this.$store.dispatch("pre",false)
          }else{
            return this.$store.dispatch("pre",true)
          }
    },
    components: {
      top
    },
    filters:{
      numberFixed (val) {
          if(val){
            return (val-0).toFixed(2)
          }
      }
    }
  }
</script>

<style scoped="scoped">
  .top_Bar{
    width: 100%;
    height: 0.5rem;
    /*overflow-y: auto;*/
    line-height: 0.58rem;
    background: #fff;
    box-shadow:0px 3px 5px #E7E7E7;
 }
  .estab_top {
    margin: 0 auto;
    width: 10rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .estab_top::-webkit-scrollbar {
    width: 0;
  }
  .pre_page{
      width:50px;
      cursor: pointer;
  }
  .person_content{
    width: 2rem;
  }
  .person_content>p{
    padding-bottom: 0.2rem;
    padding-left: 0.15rem;
    text-align: left;
  }
  .person_content>p:nth-child(1){
    padding-top: 0.2rem;
  }
  .person_content>p>a{
    color: whitesmoke;
  }
  .person_content_font{
    display: inline-block;
    margin-left: 0.1rem;
  }
  .pay{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .charge{
    text-align: center;
    margin-left: 0.05rem;
    color: #ff9900;
    display: inline-block;
    width: 40px;
    height: 25px;
    line-height: 25px;
    border:1px solid #ff9900;
    border-radius:5px;
  }
  .charge:hover{
    color: white;
    border:1px solid white;
  }
  .inpu{
    display: inline-block;
    margin:0 auto;
  }
 .input {
    margin-top: 0.1rem;
    height: 0.4rem;
    width: 4rem;
    border-radius: 0.04rem;
    padding-left: 6px;
    margin: 0.02rem auto;
  }
  .submit{
    display: inline-block;
    text-align: center;
    cursor: pointer;
    color: white;
    background: #96BD13;
    border: none;
    border-radius: 0.04rem;
    height: 0.4rem;
    line-height: 0.4rem;
    width: 3rem;
    font-size: 0.15rem;
  }
  .ic{
    position: absolute;
    left: 0.6rem;
    top: 0.10rem;
    font-size: 0.14rem;
  }
</style>



