<template>
  <div>
    <top @info="getInfo"></top>
    <div class="charge">
      <p style="display: flex;align-items: center" class="bottom">
        <span class="left_mark"></span>
        <span>充值</span>
      </p>
      <div class="bottom">
        <span class="charge_c">账户余额:</span><strong style="font-size: 0.25rem">¥ {{info.wallet}}</strong>
      </div>
      <div class="bottom">
        <span class="charge_c">账户充值</span> <Input v-model.trim="value" placeholder="请输入..." style="width: 300px"></Input>
      </div>
      <div class="bottom">
        <span class="charge_c">支付方式</span>
        <RadioGroup v-model="type" @on-change="selected">
          <Radio label="支付宝"></Radio>
          <Radio label="微信"></Radio>
      </RadioGroup>
      </div>
      <div class="bottom">
        <div style="margin-left: 0.7rem" v-if="show">
            <img :src="src" alt="" style="width: 150px;height: 150px">
            <p><a href="javascript:void(0)" class="refresh" @click="refresh">支付完成刷新界面</a></p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import top from './topBar.vue'
  export default{
    data(){
        return {
          value:"",
          type:"",
          src:"",
          show:false,
          info:''
        }
    },
    computed:{

    },
    methods:{
      getInfo(val){
        this.info = val;
      },
      selected(){
          if(this.value){
              if(this.type === '微信'){
                this.post("custom/Recharge/wxRecharge",{
                  recharge_money: this.value
                }).then( res => {
                  if (res.data.code === 10000){
                    this.show = true;
                    this.src = res.data.data;
                  }
                })
              }else{
                this.$Modal.error({
                  title: "支付宝付款失败",
                  content: "正在对接支付宝付款"
                });
              }
          }else{
            this.$Modal.error({
              title: "失败",
              content: "请先填写充值金额"
            });
          }
      },
      refresh(){
          this.$router.push('/establish');
      }
    },
    components:{
        top
    },
    mounted(){
      if(this.$route.name==="charge"){
        this.$store.dispatch("fetch_load" ,false)
      }
    }
  }
</script>
<style scoped>
  .charge{
      width: 10rem;
      margin: 0.2rem auto;
      border: 1px solid #dddddd;
      padding: 0.2rem;
  }
  .bottom{
      margin-bottom: 0.2rem;
  }
  .left_mark{
    display: inline-block;
    margin-right: 0.1rem;
    /*padding: 0.1rem 0.05rem;*/
    width: 10px;
    height: 20px;
    background-color: #8159BB;
  }
  .charge_c{
    display: inline-block;
    margin-right: 0.2rem;
  }
  .refresh{
    color: white;
    display: inline-block;
    padding: 0.1rem;
    background-color:#96BD13;
    margin-left: 0.1rem;
    margin-top: 0.1rem;
  }
</style>



